export default {
  data() {
    return {
      api: null,
      loading: true,
    };
  },
  methods: {
    async fetchData(url) {
      this.loading = true;
      this.api = null;

      await fetch(`https://json-tech-curso.vercel.app${url}`)
        .then((r) => r.json())
        .then((r) => {
          this.api = r;
          this.loading = false;
        });
    },
  },
};
