<template>
  <div v-if="loading">
    <PageLoading/>
  </div>
  <transition>
    <div v-if="api">
      <h2>{{api.nome}}</h2>
      <div class="video">
        <iframe :src="`https://www.youtube.com/embed/${api.youtube}`" title="YouTube video player" frameborder="0" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
      </div>
    </div>
  </transition>
</template>

<script>
import fetchData from '@/mixins/fetchData';
export default {
  inheritAttrs:false,
  name:"AulaView",
  props:['aula'],
  mixins:[fetchData],
  created(){
    this.fetchData(`/aula/${this.aula}`);
  },
  beforeRouteUpdate(to, from, next){
    this.fetchData(`/aula/${to.params.aula}`);
    next();
  }
}
</script>

<style scoped lang="scss">

.descricao {
  span{
    font-weight: 700;
  }  
}

 a{
    text-decoration: none;
    color: #234;
  }

  .video{
    position: relative;
    padding-bottom: 56.25%;
    iframe{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

</style>