<template>
  <div>
    <div v-if="loading">
    <PageLoading/>
  </div>
    <div v-if="api" class="conteudo">
      <div>
        <h1>{{ api.titulo }}</h1>
        <p>{{ api.descricao }}</p>
      </div>
      <ul class="cursos-lista">
        <li v-for="curso in api.cursos" :key="curso.id">
          <router-link v-slot="{referencia}" :to="{name: 'curso', params: {curso: curso.id}}">
            <h2 @click="referencia"> {{ curso.nome }}</h2>
          </router-link>
          
          <div class="descricao">
           <span>Horas: {{ curso.horas }} | </span>
           <span>Total de Aulas: {{ curso.totalAulas }}</span>
          </div>
          <p> {{ curso.descricao }} </p>
        </li>
      </ul>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import fetchData from '@/mixins/fetchData.js'

export default {
  name:"CursosView",
  mixins:[fetchData],
  created(){
    this.fetchData('/cursos');
  }
}
</script>

<style scoped lang="scss">

.descricao {
  span{
    font-weight: 700;
  }  
}

 a{
    text-decoration: none;
    color: #234;
  }

  .curso-lista{
    margin-bottom: 40px;
  }

</style>