<template>
  <div>
    <div v-if="loading">
    <PageLoading/>
  </div>
  
  <transition mode="out-in">
    <div v-if="api" class="conteudo">
      <div>
        <h1>{{ api.titulo }}</h1>
      <p>{{ api.descricao }}</p>
      </div>
      <ul>
        <li v-for="(item, index) in api.contato" :key="index">
          <b>{{ index }} :</b>
          {{ item }}
        </li>
      </ul>
    </div>
  </transition>
  </div>
  
</template>

<script>
import fetchData from '@/mixins/fetchData.js'
export default {
  name:"ContatoView",
  mixins:[fetchData],
  created(){
    this.fetchData('/contato');
  }
}
</script>

