<template>
  <div>
    <div>
      <div v-if="loading">
      <PageLoading/>
    </div>
    <div v-if="api" class="conteudo">
     <div>
      <div>
        <h2>{{ api.nome }}</h2>
      <p>Quantidade horas: {{ api.horas }} | Número de aulas: {{ api.totalAulas }}</p>
      <p>{{ api.descricao }}</p>
      <h2>Aulas</h2>
      </div>
      <ul class="aulas">
        <li v-for="aula in api.aulas" :key="aula.id">
          <router-link v-slot="{referenci}" :to="{name:'aula', params:{aula: aula.id}}">
           <span @click="referenci">{{ aula.nome }}</span>
          </router-link>
        </li>
      </ul>
     </div>
        <router-view></router-view>
    </div>
    </div>
  </div>
</template>

<script>
import fetchData from '@/mixins/fetchData';

export default {
  name: 'cursoView',
  mixins:[fetchData],
  props:["curso"],
  created(){
    this.fetchData(`/curso/${this.curso}`);
  }
}
</script>

<style scoped>
  .aulas li a{
    font-weight: 600;
    display: block;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.1);
    background: #fff;
    margin-bottom: 10px;
    border-radius: 4px;
    padding: 20px;
    text-decoration: none;
    color: #234;
  }

  .aulas li a.router-link-active{
    background: #4b8;
    color: #fff;
  }
</style>